<template>
  <div class="component-wrapper-class">
    <scroll-wrapper class="bs-wrapper-default-class">

      <div class="bscroll-content-class">
        <div v-for="(outline,index) in outlineList"
             @click="onClickOutline(index)"
             class="x-cell">

          <h1 class="x-outline-title">{{outline.imgdomainId}}</h1>
        </div>

      </div>

    </scroll-wrapper>

    <input style="height: 50px;" name="file" type="file" accept="image/*" @change="update"/>

  </div>
</template>



<script>
  import TSCenter from '../utils/TempStorageCenter'
  import ScrollWrapper from '../components/BScrollWrapper'
  import { Toast, Indicator, MessageBox} from 'mint-ui'
  export default {
    name: 'outlineList',
    components: {
      'scroll-wrapper': ScrollWrapper
    },
    data(){
      return {
        outlineList: [],
      }
    },
    computed: {
    },
    methods: {
      update(e){
        let _this = this
        let file = e.target.files[0];
        let param = new FormData(); //创建form对象
        param.append('file',file,file.name);//通过append向form对象添加数据
        param.append('chunk','0');//添加form表单中其他数据
        console.log(param.get('file')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去
        let config = {
          headers:{'Content-Type':'multipart/form-data'}
        };  //添加请求头
        this.$http.post('/qRCode/createImgDomain',param,config)
          .then(response=>{
            console.log('上传图片成功', response.data);
            _this.reqDataFromServe()
          }).catch(function (error) {
          console.log('上传图片失败',error);
        })
      },


      onClickOutline(index) {
        console.log('clickOutline index is=>', index)
        let _this = this

        let obj = _this.outlineList[index]
        console.log('set obj =>', obj)
        TSCenter.sharedInstance().setGoodsWithKey('object', obj)

        _this.$router.push({
          path: '/Contact'
        })
      },


      reqDataFromServe() {

        let _this = this;

        this.$http.post('/qRCode/getAllImgDomainsByUserId?userId=10000')
          .then(response=>{
            console.log('请求图片域成功', response.data);
            _this.outlineList = response.data;
          }).catch(function (error) {
          console.log('请求图片域失败',error);
        })

      }
    },

    activated(){
      let _this = this;
      _this.reqDataFromServe();

    }
  }
</script>

